import React from 'react';

const BlogPost = ({ index, post }) => {

  if(index % 3 === 0) {
    return (
      <div>
        {index} post multiplo de 3
      </div>
    );
  }
  else if(index % 2 === 0) {
    return (
      <div>
        {index} post multiplo de 2
      </div>
    );
  }

  return (
    <div>
      {index} post
    </div>
  );
};

export default BlogPost;
